body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
/*
 c1 - C5D7BD
 c3 - 383E56
*/



.c1{
  background-color:#C5D7BD !important;
}

.c2{
  background-color: #9FB8AD !important;
}

.c3{
  background-color: #383E56 !important;
}

.c4 {
  background-color: #FB743E;
}

.h1-ct {
    height: 90%; 
    width:100%;
    display:flex;
    align-items: center;
    justify-content: center;
}

.title-pre-bar{
  text-align: center;
  margin-top: 15px;
  margin-bottom: 8px;
  color: #C5D7BD;
  font-family: Concert One;
}

.simple-bar{
  margin: auto;
  width: 170px;
  height: 5px;
  border-radius: 2px;
  background-color: #9FB8AD;
  margin-bottom: 10px;
}

.title-pre-bar-2{
  text-align: center;
  margin-top: 15px;
  margin-bottom: 20px;
  color: white;
  font-family: Concert One;
}

.who-img{
  height: 300px;
  width: 100%;
  display: block;
  margin: auto;
  border-radius: 3px;
}

.who-txt{
  color: #9FB8AD;
  text-align: center;
  font-family: Concert One
}

.who-txt-2{
  color: #59607e;
  text-align: center;
  font-family: Concert One
}

.banner-img-ct{
  min-height:500px;
}
.product-ct{
  height:350px;
  width:100%;
  background-color:white;
  padding:10px;
  border-radius:4px;
}

.product-img{
  height:100%;
  width:100%;
  max-height: none;

}
@media (max-width:768px){
  .who-img{
    width: 80% !important;
  }
  .h1-ct{
    padding: 150px 0;
    margin: auto;
  }
  .h1-ct h1{
    font-size: 40px !important;
  }
  .banner-img-ct{
    min-height: 0px;
  }
  .banner-img-ct img{
    display: none;
  }
  .product-ct{
    height: 570px;
  }
 
}

@media (max-width:992px){
  .product-img{
    max-height:300px
  }
}

@media (max-width:768px) and (min-width:576px){
  .product-img{
    max-height: 500px !important;
  }
}

@media (max-width:576px){
  .product-img{
    max-height: 300px ;
  }  
}

@media (min-width:1000px){
  .product-img{
    max-height: 330px !important;
  }
}

.goals-h1{
  text-align:center;
    color:grey;
    font-family:Concert One;
    padding:10px 0;
}
@media (min-width:768px) and (max-width:992px){
  .goals-h1{
    font-size: 40px;
    text-align:center;
    color:grey;
    font-family:Concert One;
  }
}